@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300&family=Rubik&display=swap');
body{
  padding: 80px;
  background-color: #faf7ed;
}

#containerHeader{
  display:flex;
  align-content: space-between;
}

#profilePic{
  width: 25%;
  height: 25%;
  border-radius: 50%;
  margin-right: 20px;
}

#headerChild{
  display:flex;
  flex-direction: column;
}

#headerGranChild1{
  display: flex;
  flex-direction: column;
  font-family: Rubik, sans-serif;
  font-size: 30px;
  color:#4F0615
}

#headerChild a {
  text-decoration: none;
}

p{
  line-height: 35px;
}
#headerGranChild2{
  display: flex;
  flex-direction: row;
  font-family: Didot, sans-serif ;
  font-size: 19px;
}
#headerGranChild2 nav{
  width:332px;
  margin-right: 231px;
}

#headerGranChild2 nav ul{
  list-style: none;
  line-height: 35px;
  padding:0;
  margin-right: 10px;
}

#headerGranChild3{
  display: flex;
  flex-direction: row;
  padding:0;
}

#headerGranChild3 img{
  width:70px;
  height:70px;
  margin:5px 0 0 30px;
  border-radius: 50%;
}

#aboutMe{
  font-family: Didot,sans-serif;
  font-size: medium;
}

hr{
  width: 100%;
  color: #4F0615;
  height: 4px;
  background-color: #495E3B;
  border:none;
  margin: 15px 0 15px 0;
}

.xpAndSchool{
  display: flex;
  font-family: Didot,sans-serif;
  font-size: medium;
  align-items: flex-start;
  flex-wrap: nowrap;
}

.xpAndSchool #divLeft {
  width:80%;
  margin-right: 30px;
}

.xpAndSchool .hardSkills {
  list-style-image:url('/src/imgs/vectorProg.png');
  
}

.xpAndSchool .mktXp{
  list-style-image:url('/src/imgs/briefcase.png');
}

li{
  list-style: none;
  line-height: 35px;
  padding:0;
  margin-right: 10px;
}

.xpAndSchool .facul{
  list-style-image:url('/src/imgs/beca.png');
}

#divFacul{
  margin-right: 35px;
}
#ing{
  list-style-image:url('/src/imgs/ing\ flag.png');
}

#fra{
  list-style-image:url('/src/imgs/fra\ flag.png');
}

#esp{
  list-style-image:url('/src/imgs/esp\ flag.png');
}

